/* -------------------------------------------------------------
 * Forms - responsive
 * ---------------------------------------------------------- */
.form label,
.form .faux-label,
.form .error-msg,
.form .help-text,
.form input[type="text"],
.form input[type="password"],
.form input[type="file"],
.form input[type="url"],
.form select,
.form textarea {
  float: none;
  width: 100%;
  margin-left: 0;
  margin-right: 0;
}
.form label,
.form .faux-label,
.form .error-msg,
.form .help-text {
  display: block;
}
.form .buttons {
  padding-left: 0;
}
/* -------------------------------------------------------------
 * Tabs - responsive
 * ---------------------------------------------------------- */
.tabwrapper > .nav > li {
  float: none;
}
.tabwrapper.tabs-left > .nav,
.tabwrapper.tabs-right > .nav {
  float: none;
  width: auto;
}
.tabwrapper.tabs-left .tabpane,
.tabwrapper.tabs-right .tabpane {
  float: none;
  width: auto;
}
